<template>
<div>


    <el-table
        :data="tableData"
        style="width: 100%"
        row-key="id"
        border
        default-expand-all
        stripe
        :tree-props="{children: 'children', hasChildren: 'hasChildren'}">

        <el-table-column
            prop="name"
            label="部门"
            width="180">
        </el-table-column>
        <el-table-column
            prop="leaderName"
            label="主管"
            width="180">
        </el-table-column>
        <el-table-column
            prop="address"
            label="操作">
            <template slot-scope="scope">
                <el-button @click="showAddDialg(scope.row)" type="text" size="small">添加子部门</el-button>
                <el-button @click="showEditDialg(scope.row)" type="text" size="small">编辑</el-button>


                <el-popconfirm style="margin-left: 10px;" @confirm="deleteOrg(scope.row)"
  title="这是一段内容确定删除吗？"
>

  <el-button slot="reference" type="text" size="small"  >删除</el-button>

</el-popconfirm>

            </template>
        </el-table-column>
    </el-table>

    <org-picker title="选择部门主管"   type="user" ref="orgPicker" :selected="orgPickerSelected"
                @ok="selected"/>


    <el-dialog  destroy-on-close center @closed="beforeCloseDialog"  :title="dialogTitle" :visible.sync="dialogFormVisible">
        <el-form :model="form">
            <el-form-item label="父级部门" v-if="tableData.length>0" :label-width="formLabelWidth">
                <el-cascader style="width: 70%;" v-model="form.parent" filterable :props="tableProps" :options="tableData" :show-all-levels="false"></el-cascader>

            </el-form-item>

            <el-form-item label="部门名称" :label-width="formLabelWidth">
                <el-input style="width: 70%;" v-model="form.name" autocomplete="off"></el-input>
            </el-form-item>


            <el-form-item label="部门主管" :label-width="formLabelWidth" >
                <el-input style="width: 70%" readonly v-model="form.leader.name" autocomplete="off"></el-input>
                <el-button @click="showSelectUser" style="margin-left: 10px;"  type="primary">选择人员</el-button>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="createDep">确 定</el-button>
        </div>
    </el-dialog>

</div>
</template>

<script>
import {getOrgTreeAll} from '@/api/org'
import {createDept,updateDept,deleteDept} from '@/api/dept'
import OrgPicker from "@/components/common/OrgPicker.vue";


export default {
    name: "index",
    components: {OrgPicker},
    data(){
        return {
            dialogTitle:'',
            operFlag:1,
            tableProps:{
                value:'id',
                label:'name',
                checkStrictly:true
            },
            orgPickerSelected:[],
            dialogFormVisible:false,
            form: {
                name: '',
                leader:{

                },
                parent:[],
            },
            formLabelWidth:'120px',
            tableData: [ ]
        }
    },
    watch:{
        orgPickerSelected:function (val){
            if(val.length>0){
                this.form.leader=val[0]
            }
        }
    },
    mounted() {

this.getData();
    },
    methods: {
        beforeCloseDialog(){
            this.form={
                    name: '',
                    id:undefined,
                    parent:[],
                    leader:{

                    }
                };
                this.orgPickerSelected=[];
        },
        deleteOrg(row){
            var self=this;
            deleteDept(row).then(res=>{
              this.$message.success(res.data)
              self.getData();
          }).catch(err=>{
                this.$message.error(err.response.data)
            });
        },
        getData(){
            var t=this;
            getOrgTreeAll({deptId: 0,isDept:true}).then(rsp => {
                t.tableData=rsp.data;
            }).catch(err => {

                this.$message.error(err.response.data)
            })
        },
        showEditDialg(row){
            this.operFlag=2;
            this.dialogTitle=' 修改部门'
            console.log(row)
            this.form.parent=row.roodIdList.slice(0,row.roodIdList.length-1);
            this.form.name=row.name;
            this.form.id=row.id
            this.form.leader={
                 id:row.leaderId,
                 name:row.leaderName
            }
          this.dialogFormVisible=true;
        },
        showAddDialg(row){
            this.operFlag=1;
            this.dialogTitle='添加部门'
            this.form.parent=row.roodIdList;
          this.dialogFormVisible=true;
        },
        createDep(){
            var self=this;
            var obj={
                name:this.form.name,
                parentId:this.form.parent[this.form.parent.length-1],
                leaderUserId:this.form.leader.id,
                id:this.form.id
            }

            console.log(this.form)

            if(this.operFlag==1){
                createDept(obj).then(res=>{
                this.$message.success(res.data)
                self.getData();
                self.dialogFormVisible=false;


            }).catch(err=>{
                this.$message.error(err.response.data)
            });
            }

            if(this.operFlag==2){
                updateDept(obj).then(res=>{
                this.$message.success(res.data)
                self.getData();
                self.dialogFormVisible=false;


            }).catch(err=>{
                this.$message.error(err.response.data)
            });
            }

        },
        showSelectUser(){
            this.$refs.orgPicker.show()

        },
        selected(select) {
            this.orgPickerSelected.length = 0
            select.forEach(val => this.orgPickerSelected.push(val))
        },
    }
}
</script>

<style scoped>

</style>
