import request from '@/api/request.js'


// 创建部门
export function createDept(param) {
  return request({
    url: 'dept/create',
    method: 'post',
    data: param
  })
} 

// 修改 部门
export function updateDept(param) {
  return request({
    url: 'dept/update',
    method: 'put',
    data: param
  })
} 

// 删除部门
export function deleteDept(param) {
  return request({
    url: 'dept/delete',
    method: 'delete',
    data: param
  })
}
export default {
  createDept,updateDept,deleteDept
}
